nav {
    background: rgba(0, 0, 0, 0.3);
    width: fit-content;
    display: flex;
    padding: 0.7rem 1.7rem;
    position: fixed;
    bottom: 3rem;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 3rem;
    backdrop-filter: blur(15px);
    gap: 0.8rem;
    z-index: 999;
}

nav a {
    background: transparent;
    padding: 0.9rem;
    display: flex;
    color: var(--color-light);
    border-radius: 50%;
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.3);
    color: var(--color-white);
}

nav a.active {
    background: var(--color-bg);
    color: var(--color-white);
}

